import Enum from '~global-js/helpers/enum';
import * as getData from '~components/map-component/js/asynGetData';

export const actionTypes = new Enum(
  'INITIAL_DATA',
  'CHANGE_UNIVERSITY',
  'CHANGE_PROPERTY',
  'OPEN_MAP',
  'CLOSE_MAP',
  'RESET_FILTERS',
  'RESET_PROPERTIES',
  'UPDATE_ROUTES',
);

export function updateRoutes() {
  return {
    type: actionTypes.UPDATE_ROUTES,
    payload: null,
  };
}

export function addInitData(payload) {
  return {
    type: actionTypes.INITIAL_DATA,
    payload: {
      ...payload,
    },
  };
}

export function changeUniversity(university, showUniversityPopup = false) {
  return {
    type: actionTypes.CHANGE_UNIVERSITY,
    payload: {
      university,
      showUniversityPopup,
    },
  };
}

export function changeProperty(property) {
  return {
    type: actionTypes.CHANGE_PROPERTY,
    payload: property,
  };
}

export function openMap(propertyData = null, filters = null) {
  return {
    type: actionTypes.OPEN_MAP,
    payload: {
      selectedProperty: propertyData,
      filters,
    },
  };
}

export function resetFilters(filters, activeProperty = null) {
  return (dispatch) => {
    getData.getProperties(filters).then((response) => {
      dispatch(resetProperties(response.data, filters, activeProperty));
    });
  };
}

export function resetProperties(properties, filters = null, activeProperty = null) {
  return {
    type: actionTypes.RESET_PROPERTIES,
    payload: {
      properties,
      filters,
      activeProperty,
    },
  };
}

export function closeMap() {
  return {
    type: actionTypes.CLOSE_MAP,
  };
}



