import { Promise } from 'rsvp';
import axios from '~global-js/helpers/axios';
import { endPoints } from '~global-js/settings/paths';
import { getLocale } from '~global-js/helpers/locale';

const xhrSettings = {
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
    'Accept-Language': getLocale(),
  },
};

/**
 * @private
 */
export function getProperties(filters) {
  return new Promise((resolve, reject) => {
    const endPoint = getEndPoint(endPoints.propertiesSearch, {
      ...filters,
    });

    axios.get(endPoint, xhrSettings)
      .then((response) => {
        const returnResponse = Object.assign({}, response, {
          data: response.data.reverse(),
        });

        resolve(returnResponse);
      }).catch(() => {
        reject();
      });
  });
}

/**
 * @private
 */
export function getUniversities(filters) {
  return new Promise((resolve, reject) => {
    const endPoint = endPoints.universitiesSearch + filters.city_slug;

    axios.get(endPoint, xhrSettings)
      .then((response) => {
        resolve(response);
      }).catch(() => {
        reject();
      });
  });
}

function getEndPoint(basePath, filters) {
  const query = serialize(filters);
  return query ? `${basePath}?${query}` : basePath;
}

function serialize(queryStringParametersObject, prefix) {
  const str = [];

  Object.keys(queryStringParametersObject).forEach((key) => {
    let parameterKey = key;
    if (Object.prototype.hasOwnProperty.call(queryStringParametersObject, parameterKey)) {
      if (prefix) {
        const integerProperty = parseInt(parameterKey, 10);
        if (!isNaN(integerProperty)) {
          parameterKey = integerProperty;
        }
      }

      const computedPrefix = prefix ?
          `${prefix}[${(typeof parameterKey === 'string') ? parameterKey : ''}]` : parameterKey;
      const childProperty = queryStringParametersObject[parameterKey];

      if (typeof childProperty === 'object' && childProperty !== null) {
        str.push(serialize(childProperty, computedPrefix));
      } else {
        let currentQueryStringPart = encodeURIComponent(computedPrefix);

        if (childProperty !== null) {
          currentQueryStringPart += `=${encodeURIComponent(childProperty)}`;
        }

        str.push(currentQueryStringPart);
      }
    }
  });

  return str.join('&');
}



