import Immutable from 'immutable';
import { actionTypes } from '~components/map-component/js/actions';

export const MAP_DEFAULT_STATE = Immutable.Map({
  selectedUniversity: null,
  activeProperty: null,
  propertyInfo: {},
  universities: [],
  showUniversityPopup: false,
  updateRoutes: false,
});

export function mapReducer(state = MAP_DEFAULT_STATE, action) {
  switch (action.type) {
    case actionTypes.INITIAL_DATA: {
      let newState = state.mergeDeep(action.payload);

      if (
        action.payload.filters &&
        typeof action.payload.filters.university_slug !== 'undefined' &&
        action.payload.filters.university_slug) {
        newState = newState.merge({
          selectedUniversity: action.payload.filters.university_slug,
        });
      }
      return newState;
    }
    case actionTypes.CHANGE_UNIVERSITY: {
      return state.merge({
        selectedUniversity: action.payload.university,
        showUniversityPopup: action.payload.showUniversityPopup,
      });
    }
    case actionTypes.UPDATE_ROUTES: {
      return state.merge({
        updateRoutes: !state.get('updateRoutes'),
      });
    }
    case actionTypes.CHANGE_PROPERTY: {
      return state.merge({
        activeProperty: action.payload,
      });
    }
    case actionTypes.OPEN_MAP: {
      return state.merge({
        isVisible: true,
        ...action.payload,
      });
    }
    case actionTypes.RESET_FILTERS: {
      return state.merge({
        isVisible: true,
        ...action.payload,
      });
    }
    case actionTypes.RESET_PROPERTIES: {
      let newState;
      if (typeof action.payload.filters !== 'undefined' && action.payload.filters) {
        newState = state.merge({
          filters: action.payload.filters,
        });
      }

      if (action.payload.activeProperty) {
        return newState.merge({
          isVisible: true,
          propertiesInfo: action.payload.properties,
          activeProperty: action.payload.activeProperty,
        });
      }

      return newState.merge({
        isVisible: true,
        propertiesInfo: action.payload.properties,
      });
    }
    case actionTypes.CLOSE_MAP: {
      return state.merge({
        isVisible: false,
      });
    }
    default: {
      return state;
    }
  }
}



