/* global mapboxgl, MapboxGeocoder */
import axios from '~global-js/helpers/axios';
import polyline from 'polyline';
import handlebars from 'handlebars/runtime';
import { all as promiseAll } from 'rsvp';
import BaseComponent from '~global-js/component/base-component';
import * as actions from '~components/map-component/js/actions';
import accessToken from '~components/map-component/js/setting';
import importScript from '~global-js/helpers/import-script';
import importStyle from '~global-js/helpers/import-style';
import { formatCurrency } from '~global-js/helpers/currency';
import { mapboxGLPaths } from '~global-js/settings/paths';
import * as breakpoints from '~global-js/helpers/breakpoints';
import { event } from '~global-js/helpers/tracking';
import getImageUrl from '~global-js/helpers/image';
import generatePath from '~global-js/helpers/router';
import * as getData from '~components/map-component/js/asynGetData';
import notSupportTemplate from '~components/map-component/templates/not-support.hbs';
import propertyPinTemplate from '~components/map-component/templates/property-pin.hbs';
import propertyPopupTemplate from '~components/map-component/templates/property-popup.hbs';
import universityPopupTemplate from '~components/map-component/templates/university-popup.hbs';

const UNIVERSITY_MARKER_CLASS = 'marker marker--university';
const SELECTED_UNIVERSITY_MARKER_CLASS = 'marker marker--university marker--active';
const ROUTE_COLORS = [
  {
    color: '#38B2A6',
    opacity: 0.5,
  },
  {
    color: '#6D7FB4',
    opacity: 0.7,
  },
  {
    color: '#FD8649',
    opacity: 0.5,
  },
];
const ROUTES_WIDTH = 4;

export default class MapComponent extends BaseComponent {
  /**
   * construct the component
   *
   * @param element
   * @param props
   */
  constructor(element, props = {}) {
    if (!props.getState || !props.dispatch || !props.subscribe) {
      throw new Error('You must specify props.getState, props.dispatch and props.subscribe');
    }
    super(element, props);
    this.currentState = null;
    this.markers = {};
    this.map = null;
    this.searchResult = null;
    this.selectedUniversity = null;
  }

  /**
   * component init function
   *
   * - if browser support mapbox GL
   *   - load mapbox scripts and styles
   *   - load translation namespace
   *   - load necessary properties and universities data
   *
   * - if browser not support render a static map
   */
  init() {

    if (MapComponent.supportMapboxGL()) {

      this.properties = null;
      this.universities = null;

      promiseAll([
        importStyle(mapboxGLPaths.mapboxGLCss),
        importStyle(mapboxGLPaths.geocoderCss),
        importScript(mapboxGLPaths.mapboxGLScript),
        importScript(mapboxGLPaths.geocoderScript),
      ]).then(() => {
        const filterSection = document.querySelector('.map__filters');
        if (filterSection) {
          filterSection.classList.remove('hide');
        }

        this.props.i18n.loadNamespaces(['map', 'property_page', 'property_detail_rooms'], () => {
          if (!this.props.propertyData) {
            promiseAll([
              getData.getProperties(this.props.filters),
              getData.getUniversities(this.props.filters),
            ]).then((response) => {
              this.props.subscribe(this.reRender);
              this.props.dispatch(
                actions.addInitData({
                  selectedProperty: null,
                  filters: this.props.filters,
                  propertiesInfo: response[0].data,
                  universities: response[1].data,
                  isVisible: this.props.isVisible,
                  cordinates: MapComponent.getAllCordinates(response[0].data, response[1].data),
                  selectedUniversity: this.props.selectedUniversity,
                  activeProperty: this.props.activeProperty || null,
                })
              );
            }).catch(() => {});
          } else {
            this.props.subscribe(this.reRender);
            this.props.dispatch(
              actions.addInitData({
                selectedProperty: this.props.propertyData,
                propertiesInfo: null,
                filters: this.props.filters,
                cordinates: MapComponent.getAllCordinates([], this.props.universities),
                universities: this.props.universities,
                isVisible: this.props.isVisible,
                selectedUniversity: this.props.selectedUniversity,
                activeProperty: this.props.activeProperty || null,
              })
            );
          }
        });
      });
    } else {

      this.renderDefaultMap();
    }
  }

  /**
   * check if browser supports mapbox GL
   *
   * @returns {boolean}
   */
  static supportMapboxGL() {
    const msie = window.navigator.userAgent.indexOf('MSIE');
    const trident = window.navigator.userAgent.indexOf('Trident/');

    const canvas = document.createElement('canvas');
    const gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');

    return msie === -1 && trident === -1 && gl && gl instanceof WebGLRenderingContext;
  }

  /**
   * render default map if browser not support mapbox GL
   */
  renderDefaultMap() {
    const height = this.element.offsetHeight;

    const content = notSupportTemplate({
      url: `//api.mapbox.com/v4/mapbox.streets/${this.props.propertyData.longitude},${this.props.propertyData.latitude},12/1280x${height}.png?access_token=${accessToken}`,
    });

    this.element.innerHTML = content;
  }

  /**
   * add basci map to container
   *
   * @returns {mapboxgl.Map}
   */
  addMap() {


    mapboxgl.accessToken = accessToken;
    const map = new mapboxgl.Map({
      container: this.element,
      style: this.props.mapConf.style,
    });

    if (!this.props.mapConf.scrollZoom) {
      map.scrollZoom.disable();
    }

    if (this.props.mapConf.searchComponnet) {
      const geoCoder = new MapboxGeocoder({
        accessToken,
        placeholder: this.props.i18n.t('property_page:microapp_property.map.search.placeholder'),
      });
      map.addControl(geoCoder, this.props.mapConf.searchComponnet.position);
      this.bindListener(map, geoCoder);
    }

    if (this.props.mapConf.zoomController) {
      map.addControl(
          new mapboxgl.NavigationControl(),
          this.props.mapConf.zoomController.position
      );
    }

    if (this.props.mapConf.scaleController) {
      map.addControl(new mapboxgl.ScaleControl({
        maxWidth: 80,
      }), this.props.mapConf.scaleController.position);
    }

    this.addResizeListener();
    map.on('dblclick', () => {
      event({
        c: 'ppMaps',
        a: 'click',
        l: 'doubleClick',
      });
    });

    return map;
  }

  addResizeListener() {
    const MAP_ZOOM_IN_BTN = document.querySelector('.mapboxgl-ctrl-zoom-in');
    const MAP_ZOOM_OUT_BTN = document.querySelector('.mapboxgl-ctrl-zoom-out');
    const PROPERTY_SLUG_BANNER = document.querySelector('.hero-banner__property');

    window.addEventListener('resize', () => {
      if (this.map) {
        this.map.resize();
      }
    });

    if (MAP_ZOOM_IN_BTN && PROPERTY_SLUG_BANNER) {
      MAP_ZOOM_IN_BTN.addEventListener('click', () => {
        const PROPERTY_SLUG = PROPERTY_SLUG_BANNER.getAttribute('data-property-slug');
        event({
          c: 'ppMaps',
          a: 'click',
          l: 'zoomIn',
          mp: [{
            type: 'track',
            key: 'Clicked Property Page Map',
            params: {
              'Btn Click Type': 'Zoom In',
              'University Select': this.selectedUniversity,
              'Property Name': PROPERTY_SLUG,
            },
          }],
        });
      });
    }


    if (MAP_ZOOM_OUT_BTN && PROPERTY_SLUG_BANNER) {
      MAP_ZOOM_OUT_BTN.addEventListener('click', () => {
        const PROPERTY_SLUG = PROPERTY_SLUG_BANNER.getAttribute('data-property-slug');
        event({
          c: 'ppMaps',
          a: 'click',
          l: 'zoomOut',
          mp: [{
            type: 'track',
            key: 'Clicked Property Page Map',
            params: {
              'Btn Click Type': 'Zoom Out',
              'University Select': this.selectedUniversity,
              'Property Name': PROPERTY_SLUG,
            },
          }],
        });
      });
    }
  }

  /**
   * plugin listeners
   *
   * @param map
   * @param geoCoder
   */
  bindListener(map, geoCoder) {
    const self = this;
    geoCoder.on('result', (result) => {
      if (self.searchResult) {
        self.searchResult.remove();
      }

      // add a maker for result.center
      self.searchResult = new mapboxgl.Popup({ closeOnClick: true })
        .setLngLat(result.result.center)
        .setHTML(result.result.place_name);
      self.searchResult.addTo(map);
    });

    geoCoder.on('clear', () => {
      if (self.searchResult) {
        self.searchResult.remove();
      }
      this.resetMap(map);
    });
  }

  convertData() {
    if (this.currentState.get('propertiesInfo')) {
      const properties = {};

      this.currentState.get('propertiesInfo').map((propertyMap) => {
        const property = propertyMap.toJS();
        properties[property.slug] = property;

        return properties;
      });

      this.properties = properties;
    } else {
      this.properties = null;
    }

    if (this.currentState.get('universities')) {
      const universities = {};

      this.currentState.get('universities').map((universityMap) => {
        const university = universityMap.toJS();
        universities[university.slug] = university;

        return universities;
      });

      this.universities = universities;
    } else {
      this.universities = null;
    }
  }

  /**
   * reRender the map once the state changed
   *
   * - if map is visible
   *    - if map hasn't been initialized before,
   *      initialize the map and add universities and properties
   *    - if map has been initialized before, just update the universities and properties
   * - if map is not visible
   *    - hide the map
   */
  reRender = () => {
    const previousValue = this.currentState;
    this.currentState = this.props.getState();
    const isVisible = this.currentState.get('isVisible') || this.props.isVisible;
    this.convertData();

    // if it's visible then execute
    if (isVisible) {
      // if the map already exists
      if (!document.querySelector('.mapboxgl-canvas-container')) {
        this.element.classList.add('map__container--active');
        this.map = this.addMap();
        this.addUniversities();
        this.addProperties();

        this.map.on('click', (e) => {
          const target = e.originalEvent.target;
          if (!target.classList.contains('marker--popup') && !target.closest('.marker--popup')) {
            const popups = [].slice.call(document.querySelectorAll('.popup'));
            popups.forEach((popup) => {
              popup.parentElement.classList.remove('marker--popup');
            });
          }
        });
      } else {
        this.element.classList.add('map__container--active');

        const previousSelectedUniversity = previousValue.get('selectedUniversity');
        const currentSelectedUniversity = this.currentState.get('selectedUniversity');
        const previousSelectedProperty = previousValue.get('activeProperty');
        const currentSelectedProperty = this.currentState.get('activeProperty');

        // when university changes
        if (
          currentSelectedUniversity != null &&
          currentSelectedUniversity !== previousSelectedUniversity
        ) {
          this.selectedUniversity = currentSelectedUniversity;
          this.toggleSelectedUniversity(currentSelectedUniversity, this.currentState.get('showUniversityPopup'));
        }

        // when property is set to null
        if (previousSelectedProperty && !currentSelectedProperty) {
          const activeProperty = document.querySelector('.marker--active.marker--property');
          if (activeProperty) {
            activeProperty.classList.remove('marker--active');
            activeProperty.classList.remove('marker--popup');
          }

          this.removeRoutes();

          if (this.currentState.get('selectedUniversity') && this.currentState.get('selectedUniversity') !== 'reset-map') {
            const selectedUniversity = this.universities[this.currentState.get('selectedUniversity')];

            this.map.flyTo({
              center: [selectedUniversity.longitude, selectedUniversity.latitude],
            });
          }
        }

        if (
          previousValue.get('propertiesInfo') &&
          previousValue.get('propertiesInfo').toJS() &&
          this.currentState.get('propertiesInfo') &&
          this.currentState.get('propertiesInfo').toJS() &&
          previousValue.get('propertiesInfo').toJS().length !== this.currentState.get('propertiesInfo').toJS().length
        ) {

          this.resetProperties();
        } else if (document.querySelector('.map__loading--active')) {
          document.querySelector('.map__loading--active').classList.remove('map__loading--active');
        }
      }
    } else if (!isVisible && document.querySelector('.mapboxgl-canvas-container')) {
      // if map is visible and map exists, show map
      this.element.classList.remove('map__container--active');
    }
  };

  /**
   * add properties to the map
   */
  addProperties() {

    if (this.currentState.get('propertiesInfo')) {
      const properties = this.currentState.get('propertiesInfo').toJS();
      properties.forEach((property) => {
        this.addProperty(property);
      });
    } else {
      this.addProperty(this.currentState.get('selectedProperty').toJS());
    }

    this.resetMap();
  }

  /**
   * add property to the map and bind click listener
   *
   * @param property
   */
  addProperty(property) {
    const el = document.createElement('div');
    const price = property.state === 'available_with_price' ? formatCurrency(this.props.i18n, property.from_price, property.currency) : null;
    const discountedPrice = property.discounted_from_price ?
        formatCurrency(this.props.i18n, property.discounted_from_price, property.currency) : null;

    const content = propertyPinTemplate({
      state: property.state ? property.state.replace(/_/g, '-') : 'sold-out',
      price,
      discountedPrice,
      property,
      isPP: this.props.propertyData,
    });

    el.className = 'marker marker--property';
    el.innerHTML = content;
    const formattedPrice =
      this.getFormattedPrice(property.state, price, discountedPrice, property.billing_cycle);

    if (!this.props.propertyData) {
      el.addEventListener('click', () => {
        this.removeRoutes();
        MapComponent.removePopups('property');

        if (this.currentState.get('activeProperty') === property.slug) {
          el.classList.add('marker--active');
          el.classList.add('marker--popup');
        } else {
          this.addPropertyPopup(el, property, formattedPrice);
          this.props.dispatch(actions.changeProperty(property.slug));
        }

        const propertyField = document.querySelector('.map__filter--property');
        if (propertyField) {
          propertyField.innerHTML = property.name;
        }

        this.addRoutes();
      });
    }

    // When render map first time, if current property is the active property, then fly to it
    if (this.currentState.get('activeProperty') === property.slug) {
      this.addPropertyPopup(el, property, formattedPrice);
      this.map.flyTo({
        center: [property.longitude, property.latitude],
        zoom: 11,
      });
    }

    // add marker to map
    new mapboxgl.Marker(el)
      .setLngLat([property.longitude, property.latitude])
      .addTo(this.map);
  }

  /**
   * add property popup
   */

  addPropertyPopup(el, property, formattedPrice) {
    if (el.firstChild) {
      el.firstChild.classList.add('interacted');
    }

    const previousPopups = [].slice.call(document.querySelectorAll('.popup'));

    previousPopups.forEach((previousPopup) => {
      const marker = previousPopup.parentElement;
      if (previousPopup && marker) {
        marker.classList.remove('marker--popup');
        const pin = previousPopup.parentElement.querySelector('.university');
        if (pin) {
          pin.classList.remove('interacted');
        }
      }
    });

    const filters = this.currentState.get('filters');
    const link = generatePath('property_detail', {
      countrySlug: filters.get('country_slug'),
      citySlug: filters.get('city_slug'),
      propertySlug: property.slug,
    });

    if (!el.querySelector('popup')) {
      const clickedPopup = document.createElement('div');
      const popupContent = this.renderTemplate(propertyPopupTemplate, {
        image: getFormattedHeroImage(property.hero_image, property.slug, 'property_preview_mobile'),
        link,
        slug: property.slug,
        name: property.name,
      });

      clickedPopup.className = 'popup popup--property';
      clickedPopup.innerHTML = popupContent;
      clickedPopup.querySelector('.popup__price').innerHTML = formattedPrice;
      el.insertBefore(clickedPopup, this.firstChild);
    }

    el.classList.add('marker--active');
    el.classList.add('marker--popup');
  }

  /**
   * if filters changed in SRP, reset properties
   */
  resetProperties() {
    const propertyMarkers = [].slice.call(document.querySelectorAll('.marker--property'));

    if (propertyMarkers.length > 0) {
      const propertyMarkerParent = document.querySelector('.marker--property').parentElement;

      propertyMarkers.forEach((marker) => {
        propertyMarkerParent.removeChild(marker);
      });
    }

    this.addProperties();
  }

  /**
   * add universities to the map
   */
  addUniversities() {


    const universities = this.currentState.get('universities').toJS();

    universities.forEach((university) => {
      const slug = university.slug;
      const longitude = university.longitude;
      const latitude = university.latitude;
      if (slug === this.currentState.get('selectedUniversity')) {
        this.addUniversity(
            SELECTED_UNIVERSITY_MARKER_CLASS,
            university);
        // fit bound to the property and current selected university
        if (this.props.propertyData && !this.currentState.get('activeProperty')) {
          this.fitToBounds(
            [[this.props.propertyData.longitude, this.props.propertyData.latitude],
            [longitude, latitude]]
          );
        }
      } else {
        this.addUniversity(
            UNIVERSITY_MARKER_CLASS,
            university);
      }
      return this.map;
    });
  }

  /**
   * update university when the selectedUniversity changed
   *
   * @param {string} selectedUniversity
   * @param {boolean} showPopup
   */
  toggleSelectedUniversity(selectedUniversity, showPopup = false) {
    if (this.searchResult) {
      this.searchResult.remove();
    }

    this.removeRoutes();
    MapComponent.removePopups('university');

    const previousSelected = document.querySelector('.marker--university.marker--active');
    if (previousSelected) {
      previousSelected.classList.remove('marker--active');
    }

    if (this.props.universityChangeCallback) {
      this.props.universityChangeCallback(selectedUniversity);
    }

    // IE auto set to '1' if I set value = '', so can't use empty value
    if (selectedUniversity === 'reset-map') {
      if (breakpoints.isLargeScreen()) {
        const universityList = document.querySelector('.map__university--selected');
        if (universityList) {
          universityList.innerText = this.props.i18n.t('property_page:microapp_property.map.search_university');
        }
      } else {
        const universityList = document.querySelector('.map__university-list');
        if (universityList) {
          universityList.selectedIndex = 0;
        }
      }

      let property = this.props.propertyData || null;
      if (!property && this.currentState.get('activeProperty')) {
        property = this.properties[this.currentState.get('activeProperty')];
      }

      if (property) {
        this.map.flyTo({
          center: [property.longitude, property.latitude],
        });
      }
    } else {
      const marker = document.querySelector(`.university--${selectedUniversity}`);

      if (!marker.querySelector('.popup') && showPopup) {
        this.addSelectedUniversityPopup(marker);
      }
      marker.classList.add('marker--active');

      if (showPopup) {
        marker.classList.add('marker--popup');
      }

      const university = JSON.parse(marker.getAttribute('data-university'));
      let universityList = null;
      const universityName = university.name;
      if (breakpoints.isLargeScreen()) {
        universityList = document.querySelector('.map__university--selected');
        if (universityList) {
          universityList.innerText = universityName;
        }
      } else {
        universityList = document.querySelector('.map__university-list');
        if (universityList) {
          universityList.value = selectedUniversity;
        }

        if (this.props.clickUniversityCallback) {
          this.props.clickUniversityCallback(selectedUniversity);
        }
      }

      // fit bound to the property and current selected university
      if (this.props.propertyData === null && this.currentState.get('activeProperty') === null) {
        this.map.flyTo({
          center: [university.longitude, university.latitude],
          zoom: 15,
        });
      } else {
        this.addRoutes();
      }
    }
  }

  /**
   * remove all popups
   */
  static removePopups(type) {
    const popups = [].slice.call(document.querySelectorAll('.popup'));
    popups.forEach((popup) => {
      if (popup.parentElement) {
        if (popup.parentElement.classList.contains(`marker--${type}`)) {
          popup.parentElement.classList.remove('marker--active');
        }

        popup.parentElement.classList.remove('marker--popup');
      }
    });
  }

  /**
   * remove all routes
   */
  removeRoutes() {
    for (let i = 0; i < 3; i++) {
      if (this.map.getLayer(`route${i}`)) {
        this.map.removeLayer(`route${i}`);
      }

      if (this.map.getSource(`route${i}`)) {
        this.map.removeSource(`route${i}`);
      }

      if (document.querySelector('.marker--route')) {
        document.querySelector('.marker--route').remove();
      }
    }
  }

  /**
   * check routes exists
   */
  checkRoutes(index) {
    return this.map.getLayer(`route${index}`);
  }

  /**
   * generate the url to request routes
   *
   * @param {string} type
   * @param {number} longitude1
   * @param {number} latitude1
   * @param {number} longitude2
   * @param {number} latitude2
   *
   */
  static generateRoutesUrl(type, longitude1, latitude1, longitude2, latitude2) {
    let communRoutes = mapboxGLPaths.communRoutes;
    communRoutes = communRoutes.replace('{type}', type)
      .replace('{longitude1}', longitude1)
      .replace('{latitude1}', latitude1)
      .replace('{longitude2}', longitude2)
      .replace('{latitude2}', latitude2);
    return communRoutes + accessToken;
  }

  /**
   * add routes to the map
   * @param {object} property
   * @param {object} university
   */
  addRoutes() {


    const universitySlug = this.currentState.get('selectedUniversity');
    const university =
      this.universities && universitySlug ? this.universities[universitySlug] : null;
    const propertySlug = this.currentState.get('activeProperty');
    let property = this.properties && propertySlug ? this.properties[propertySlug] : null;
    const routeMapping = ['walking', 'cycling', 'driving'];

    if (!property && this.props.propertyData) {
      property = this.props.propertyData;
    }

    if (property && university) {
      axios.all([
        axios.get(MapComponent.generateRoutesUrl('walking', property.longitude, property.latitude, university.longitude, university.latitude)),
        axios.get(MapComponent.generateRoutesUrl('cycling', property.longitude, property.latitude, university.longitude, university.latitude)),
        axios.get(MapComponent.generateRoutesUrl('driving', property.longitude, property.latitude, university.longitude, university.latitude)),
      ])
      .then((responses) => {
        let routeCount = 0;
        responses.forEach((response, index) => {
          if (!this.checkRoutes(routeCount)) {
            let geometry = response.data.routes[0].geometry;
            geometry = typeof geometry === 'string' ? polyline.toGeoJSON(geometry) : geometry;
            const markerIndex = Math.round(geometry.coordinates.length / 2) - (1 - routeCount);
            const markerCoordinate = typeof (geometry.coordinates[markerIndex]) !== 'undefined' ?
                geometry.coordinates[markerIndex] : geometry.coordinates[0];
            const markerDuration = Math.round(response.data.routes[0].duration / 60);
            const feature = {
              type: 'Feature',
              properties: {},
              geometry,
            };

            this.map.addSource(`route${routeCount}`, {
              type: 'geojson',
              data: feature,
            });

            this.map.addLayer({
              id: `route${routeCount}`,
              type: 'line',
              source: `route${routeCount}`,
              layout: {
                'line-join': 'round',
                'line-cap': 'round',
              },
              paint: {
                'line-color': ROUTE_COLORS[routeCount].color,
                'line-opacity': ROUTE_COLORS[routeCount].opacity,
                'line-width': ROUTES_WIDTH,
              },
            });

            const el = document.createElement('div');
            const commuteTime = markerDuration >= 2 ? this.props.i18n.t('map:map.popup.commute_time.minutes') : this.props.i18n.t('map:map.popup.commute_time.minute');


            el.innerHTML = `<span class="marker--route-content marker--route-${routeMapping[index]}">${markerDuration} ${commuteTime}</span>`;
            el.className = 'marker marker--route';
            new mapboxgl.Marker(el, { offset: [-40, -24] })
                .setLngLat(markerCoordinate)
                .addTo(this.map);
          }

          routeCount += 1;
        });
      });

      this.fitToBounds(
        [
          [property.longitude, property.latitude],
          [university.longitude, university.latitude],
        ]
      );
    }
  }

  /**
   * fly view to the property
   */
  flyToProperty() {
    if (this.currentState.get('selectedProperty')) {
      const propertyData = this.currentState.get('selectedProperty').toJS();
      this.map.flyTo({
        center: [propertyData.longitude, propertyData.latitude],
      });
    }
  }

  /**
   * bind translation helper before render template
   */
  onBeforeTemplateRender() {
    handlebars.registerHelper('trans', key => this.props.i18n.t(key));
  }

  /**
   * rewrite render template function to do additional work
   *
   * @param template
   * @param props
   * @returns {*}
   */
  renderTemplate(template, props) {
    this.onBeforeTemplateRender();
    const result = template(props);
    onAfterTemplateRender();
    return result;
  }


  /**
   * add a university to the map
   *
   * @param className
   * @param offset
   * @param university
     */
  addUniversity(className, university) {

    const lngLat = [university.longitude, university.latitude];
    const slug = university.slug;
    const el = document.createElement('div');
    const PROPERTY_SLUG_BANNER = document.querySelector('.hero-banner__property');
    el.className = `${className} university--${university.slug}`;
    el.setAttribute('data-university', JSON.stringify(university));

    if (className === SELECTED_UNIVERSITY_MARKER_CLASS) {
      el.innerHTML = '<div class="university interacted"></div>';
    } else {
      el.innerHTML = '<div class="university"></div>';
    }

    const marker = new mapboxgl.Marker(el)
        .setLngLat(lngLat);

    marker.addTo(this.map);
    this.markers[slug] = marker;

    el.addEventListener('click', () => {
      if (slug === this.currentState.get('selectedUniversity')) {
        this.addSelectedUniversityPopup(el);
        el.classList.add('marker--active');
      } else {
        this.props.dispatch(actions.changeUniversity(slug, true));
      }

      if (PROPERTY_SLUG_BANNER) {
        const PROPERTY_SLUG = PROPERTY_SLUG_BANNER.getAttribute('data-property-slug');
        event({
          c: 'ppMaps',
          a: 'click',
          l: `universityClicked:${slug}`,
          mp: [{
            type: 'track',
            key: 'Clicked Property Page Map',
            params: {
              'Btn Click Type': 'University on Map',
              'University Select': slug,
              'Property Name': PROPERTY_SLUG,
            },
          }],
        });
      }
    });
  }


  /**
   * add university popup
   *
   * @param university
   * @param el
     */
  addSelectedUniversityPopup(el) {
    const university = JSON.parse(el.getAttribute('data-university'));

    const popup = document.createElement('div');
    popup.className = 'popup popup--university';
    const content = this.renderTemplate(universityPopupTemplate, {
      image: getFormattedHeroImage(university.hero_image, university.slug, 'hero_banner_mobile'),
      slug: university.slug,
      name: university.name,
    });
    popup.innerHTML = content;
    el.classList.add('marker--popup');
    el.insertBefore(popup, el.firstChild);
  }

  /**
   * reset map to initial view
   */
  resetMap() {

    const allCordinates = this.currentState.get('cordinates') || [];

    if (this.currentState.get('activeProperty') === null) {
      const selectedUniversity = this.currentState.get('selectedUniversity');
      if (selectedUniversity && selectedUniversity !== 'reset-map') {
        this.map.flyTo({
          center: [
            this.universities[selectedUniversity].longitude,
            this.universities[selectedUniversity].latitude,
          ],
          zoom: 15,
        });
      } else {
        this.fitToBounds(allCordinates.toJS());
      }
    }

    if (document.querySelector('.map__loading--active')) {
      document.querySelector('.map__loading--active').classList.remove('map__loading--active');
    }
  }

  /**
   * get all cordinates from properties and universities
   *
   * @param properties
   * @param universities
   * @returns {Array}
     */
  static getAllCordinates(properties, universities) {

    const cordinates = [];

    properties.forEach((property) => {
      cordinates.push([property.longitude, property.latitude]);
    });

    universities.forEach((university) => {
      cordinates.push([university.longitude, university.latitude]);
    });
    return cordinates;
  }

  /**
   * fit view to a bounds
   *
   * @param coordinates
     */
  fitToBounds(coordinates) {


    this.map.resize();
    let padding = 128;
    let offset = [0, 0];
    const tabSection = document.querySelector('.map__tabs');
    const tabHeight = tabSection ? tabSection.offsetHeight : 0;

    if (breakpoints.isLargeScreen()) {
      padding = 128;
      if (document.querySelector('.property-pp')) {
        const right = (document.querySelector('.map__container--active').offsetWidth - 1008) / 2;
        const topRightController = document.querySelector('.mapboxgl-ctrl-top-right');
        const width = topRightController ? topRightController.offsetWidth : 0;
        offset = [(-width - right) / 2, 0];
      } else {
        const sidebar = document.querySelector('.map-sidebar-filter');

        if (sidebar) {
          offset = [(sidebar.offsetWidth + 32) / 2, 0];
        }
      }
    } else if (breakpoints.isMediumScreen()) {
      padding = 80;
      offset = [0, -tabHeight / 2];
    } else {
      padding = 64;
      offset = [0, -tabHeight / 2];
    }

    const bounds = coordinates.reduce(
      (newBounds, coord) => (
        newBounds.extend(coord)
      ),
      new mapboxgl.LngLatBounds(coordinates[0], coordinates[0])
    );

    this.map.fitBounds(bounds, { padding, offset });
  }

  /**
   * get formatted price
   *
   * @param state
   * @param fromPrice
   * @param discountedFromPrice
   * @param period
     * @returns {*}
     */
  getFormattedPrice(state, fromPrice, discountedFromPrice, period) {
    if (state === 'available_with_price') {
      const fromLabel = this.props.i18n.t('global.label.from');
      const pricingPeriod = this.props.i18n.t(`property_detail_rooms:property_detail.rooms.text.price_scope_period.per_${period}`);
      let price;
      if (discountedFromPrice) {
        price = `<span class="current-price">${discountedFromPrice}  </span><span class="old-price">${fromPrice}</span>`;
      } else {
        price = `<span class="current-price">${fromPrice}</span>`;
      }

      const translatedString = this.props.i18n.t('global.property_listing.formatted_price', {
        price,
        fromLabel,
        pricingPeriod,
      });

      return translatedString;
    } else if (state === 'sold_out') {
      return this.props.i18n.t('property_detail_rooms:property_detail.rooms.unit.price.label.sold_out');
    } else if (!state) {
      return this.props.i18n.t('property_detail_rooms:property_detail.rooms.text.not_available');
    }

    return this.props.i18n.t('property_detail_rooms:property_detail.rooms.unit.price.label.coming_soon');
  }

  // eslint-disable-next-line class-methods-use-this
  destroy() {
  }
}


/**
 * get formatted hero image
 *
 * @param imageData
 * @param slug
 * @param imageFilter
 * @returns {*}
 */
function getFormattedHeroImage(imageData, slug, imageFilter) {
  if (!imageData) {
    return null;
  }

  return {
    alt_text: imageData.alt_text ? imageData.alt_text : slug,
    src: getImageUrl(imageData.source, imageFilter),
  };
}


/**
 * unbind translation helper after render the template
 */
function onAfterTemplateRender() {
  handlebars.unregisterHelper('trans');
}



